
'use client'; // Make this a client component

import type { SubmitHandler } from 'react-hook-form';
import React, { useState, useRef, useMemo, useEffect, useCallback } from 'react';
import { useForm } from 'react-hook-form';
import { zodResolver } from '@hookform/resolvers/zod';
import { z } from 'zod';
import * as XLSX from 'xlsx'; // Import xlsx library
import {
  Table,
  TableBody,
  TableCaption,
  TableCell,
  TableHead,
  TableHeader,
  TableRow,
} from "@/components/ui/table";
import { Card, CardContent, CardHeader, CardTitle, CardDescription } from "@/components/ui/card";
import { Button } from "@/components/ui/button";
import { Input } from "@/components/ui/input";
import { Form, FormControl, FormField, FormItem, FormLabel, FormMessage } from '@/components/ui/form';
import { Checkbox } from "@/components/ui/checkbox"; // Import Checkbox
import { useToast } from '@/hooks/use-toast'; // Import useToast
import { Separator } from '@/components/ui/separator'; // Import Separator
import Link from 'next/link'; // Import Link
import { ArrowLeft, PlusCircle, X, Upload, Download, FileUp, Loader2, Trash2, AlertTriangle } from 'lucide-react'; // Import ArrowLeft, PlusCircle, X, Upload, Download, Trash2, AlertTriangle icons
import { approvalSchema, type ApprovalFormData } from '@/data/approval-data'; // Import shared data
import {
  AlertDialog,
  AlertDialogAction,
  AlertDialogCancel,
  AlertDialogContent,
  AlertDialogDescription,
  AlertDialogFooter,
  AlertDialogHeader,
  AlertDialogTitle,
  AlertDialogTrigger,
} from "@/components/ui/alert-dialog" // Import AlertDialog components

// Define expected headers for the Excel template (case-insensitive check later)
const excelHeaders = [
    "Marka",
    "Tip Onay No",
    "Varyant",
    "Versiyon",
    "Seri Tadilat Tip Onayı"
];

// Add a unique ID to each data entry for easier identification during delete
interface ApprovalDataWithId extends ApprovalFormData {
  id: string; // Or number, if preferred
}

const LOCAL_STORAGE_KEY = 'seriTadilatOnayData';

export default function SeriTadilatOnayPage() {
  const [approvalData, setApprovalData] = useState<ApprovalDataWithId[]>([]);
  const [isFormVisible, setIsFormVisible] = useState(false);
  const [isUploading, setIsUploading] = useState(false);
  const [selectedRows, setSelectedRows] = useState<Set<string>>(new Set());
  const { toast } = useToast();
  const fileInputRef = useRef<HTMLInputElement>(null);
  const nextId = useRef(0); // Used to generate unique IDs for new entries

  // Load data from localStorage on component mount
  useEffect(() => {
    try {
      const storedData = localStorage.getItem(LOCAL_STORAGE_KEY);
      if (storedData) {
        const parsedData: ApprovalDataWithId[] = JSON.parse(storedData);
        setApprovalData(parsedData);

        // Update nextId based on loaded data to prevent collisions
        let maxNumericId = -1;
        parsedData.forEach(item => {
          if (item.id.startsWith('new-')) {
            const numericPart = parseInt(item.id.substring(4), 10);
            if (!isNaN(numericPart) && numericPart > maxNumericId) {
              maxNumericId = numericPart;
            }
          }
        });
        nextId.current = maxNumericId + 1;

      } else {
        // Initialize with an empty array if no data in localStorage
        setApprovalData([]);
      }
    } catch (error) {
      console.error("Failed to load data from localStorage", error);
      toast({
        title: "Veri Yükleme Hatası",
        description: "Lokal depolamadan veriler yüklenirken bir sorun oluştu.",
        variant: "destructive",
      });
      setApprovalData([]); // Fallback to empty array on error
    }
  }, [toast]);

  // Save data to localStorage whenever approvalData changes
  useEffect(() => {
    try {
      localStorage.setItem(LOCAL_STORAGE_KEY, JSON.stringify(approvalData));
    } catch (error) {
      console.error("Failed to save data to localStorage", error);
      toast({
        title: "Veri Kaydetme Hatası",
        description: "Veriler lokal depolamaya kaydedilirken bir sorun oluştu.",
        variant: "destructive",
      });
    }
  }, [approvalData, toast]);


  const form = useForm<ApprovalFormData>({
    resolver: zodResolver(approvalSchema),
    defaultValues: {
      marka: "",
      tipOnayNo: "",
      varyant: "",
      versiyon: "",
      seriTadilatTipOnayi: "",
    },
  });

  const onSubmit: SubmitHandler<ApprovalFormData> = (data) => {
    const exists = approvalData.some(
      (item) =>
        item.marka.toLowerCase() === data.marka.toLowerCase() &&
        item.tipOnayNo.toLowerCase() === data.tipOnayNo.toLowerCase() &&
        item.varyant.toLowerCase() === data.varyant.toLowerCase() &&
        item.versiyon.toLowerCase() === data.versiyon.toLowerCase() &&
        item.seriTadilatTipOnayi.toLowerCase() === data.seriTadilatTipOnayi.toLowerCase()
    );

    if (exists) {
       toast({
         title: "Tekrarlanan Veri",
         description: "Bu veri zaten listede mevcut.",
         variant: "destructive",
       });
       return;
    }

    const newDataEntry: ApprovalDataWithId = {
       ...data,
       id: `new-${nextId.current++}` // Generate a new unique ID
    }

    setApprovalData((prevData) => [...prevData, newDataEntry]);
    form.reset();
    setIsFormVisible(false);
    toast({
      title: "Veri Eklendi",
      description: "Yeni tip onay verisi başarıyla listeye eklendi.",
    });
  };

  const handleCancel = () => {
    form.reset();
    setIsFormVisible(false);
  };

  const handleDeleteSingle = (idToDelete: string) => {
    setApprovalData((prevData) => prevData.filter((item) => item.id !== idToDelete));
    setSelectedRows(prevSelected => {
      const newSelected = new Set(prevSelected);
      newSelected.delete(idToDelete);
      return newSelected;
    });
    toast({
      title: "Veri Silindi",
      description: "Seçili tip onay verisi başarıyla listeden silindi.",
      variant: "destructive",
    });
  };

  const handleDeleteAll = () => {
    setApprovalData([]);
    setSelectedRows(new Set());
    toast({
      title: "Tüm Veriler Silindi",
      description: "Listedeki tüm tip onay verileri başarıyla silindi.",
      variant: "destructive",
    });
  };

  const handleDeleteSelected = () => {
    setApprovalData((prevData) => prevData.filter((item) => !selectedRows.has(item.id)));
    setSelectedRows(new Set());
    toast({
        title: `${selectedRows.size} Veri Silindi`,
        description: "Seçili tip onay verileri başarıyla listeden silindi.",
        variant: "destructive",
    });
  };

  const handleRowSelect = (rowId: string, isSelected: boolean | 'indeterminate') => {
      setSelectedRows(prevSelected => {
          const newSelected = new Set(prevSelected);
          if (isSelected === true) {
              newSelected.add(rowId);
          } else {
              newSelected.delete(rowId);
          }
          return newSelected;
      });
  };

  const handleSelectAll = (isChecked: boolean | 'indeterminate') => {
      if (isChecked === true) {
          const allIds = new Set(sortedApprovalData.map(item => item.id));
          setSelectedRows(allIds);
      } else {
          setSelectedRows(new Set());
      }
  };

   const isAllSelected = useMemo(() => {
        if (approvalData.length === 0) return false;
        return selectedRows.size === approvalData.length;
    }, [selectedRows, approvalData.length]);

    const isIndeterminate = useMemo(() => {
        return selectedRows.size > 0 && selectedRows.size < approvalData.length;
    }, [selectedRows, approvalData.length]);

    const headerCheckboxState = isAllSelected ? true : (isIndeterminate ? 'indeterminate' : false);


   const triggerFileInput = () => {
     fileInputRef.current?.click();
   };

   const handleFileChange = (event: React.ChangeEvent<HTMLInputElement>) => {
     const file = event.target.files?.[0];
     if (!file) return;

     if (!file.name.endsWith('.xlsx') && !file.name.endsWith('.xls') && !file.name.endsWith('.csv')) {
         toast({
             title: "Geçersiz Dosya Türü",
             description: "Lütfen bir Excel (.xlsx, .xls) veya CSV (.csv) dosyası yükleyin.",
             variant: "destructive",
         });
         if (fileInputRef.current) fileInputRef.current.value = '';
         return;
     }

     setIsUploading(true);
     const reader = new FileReader();

     reader.onload = (e) => {
       const data = e.target?.result;
       if (!data) {
           setIsUploading(false);
           toast({ title: "Dosya Okuma Hatası", description: "Dosya içeriği okunamadı.", variant: "destructive" });
           return;
       }

       try {
         const workbook = XLSX.read(data, { type: 'array' });
         const sheetName = workbook.SheetNames[0];
         const worksheet = workbook.Sheets[sheetName];
         const jsonData = XLSX.utils.sheet_to_json(worksheet, { header: 1 }) as string[][];

         if (jsonData.length < 2) {
            throw new Error("Dosya boş veya sadece başlık satırı içeriyor.");
         }

         const headerRow = jsonData[0].map(h => h?.trim() || '');
         const expectedHeadersLower = excelHeaders.map(h => h.toLowerCase());
         const actualHeadersLower = headerRow.map(h => h.toLowerCase());

         const missingHeaders = expectedHeadersLower.filter(eh => !actualHeadersLower.includes(eh));
         if (missingHeaders.length > 0) {
             throw new Error(`Eksik sütun başlıkları: ${missingHeaders.join(', ')}. Lütfen şablonu kullanın.`);
         }

         const headerIndexMap: { [key: string]: number } = {};
         actualHeadersLower.forEach((h, index) => {
             if (expectedHeadersLower.includes(h)) {
                 headerIndexMap[h] = index;
             }
         });

         const newData: ApprovalDataWithId[] = [];
         const validationErrors: string[] = [];
         let duplicateCount = 0;

         for (let i = 1; i < jsonData.length; i++) {
           const row = jsonData[i];
           if (!row || row.every(cell => !cell || String(cell).trim() === '')) {
             continue;
           }

           const rowData: ApprovalFormData = {
              marka: String(row[headerIndexMap['marka']] ?? '').trim(),
              tipOnayNo: String(row[headerIndexMap['tip onay no']] ?? '').trim(),
              varyant: String(row[headerIndexMap['varyant']] ?? '').trim(),
              versiyon: String(row[headerIndexMap['versiyon']] ?? '').trim(),
              seriTadilatTipOnayi: String(row[headerIndexMap['seri tadilat tip onayı']] ?? '').trim(),
           };

           const validationResult = approvalSchema.safeParse(rowData);

           if (!validationResult.success) {
             validationErrors.push(`Satır ${i + 1}: ${validationResult.error.errors.map(e => `${excelHeaders[Object.keys(rowData).indexOf(e.path[0] as string)]}: ${e.message}`).join(', ')}`);
           } else {
             const isDuplicate = approvalData.some(
               (item) =>
                 item.marka.toLowerCase() === rowData.marka.toLowerCase() &&
                 item.tipOnayNo.toLowerCase() === rowData.tipOnayNo.toLowerCase() &&
                 item.varyant.toLowerCase() === rowData.varyant.toLowerCase() &&
                 item.versiyon.toLowerCase() === rowData.versiyon.toLowerCase() &&
                 item.seriTadilatTipOnayi.toLowerCase() === rowData.seriTadilatTipOnayi.toLowerCase()
             ) || newData.some(
                (item) =>
                 item.marka.toLowerCase() === rowData.marka.toLowerCase() &&
                 item.tipOnayNo.toLowerCase() === rowData.tipOnayNo.toLowerCase() &&
                 item.varyant.toLowerCase() === rowData.varyant.toLowerCase() &&
                 item.versiyon.toLowerCase() === rowData.versiyon.toLowerCase() &&
                 item.seriTadilatTipOnayi.toLowerCase() === rowData.seriTadilatTipOnayi.toLowerCase()
             );

             if (!isDuplicate) {
                 newData.push({
                     ...validationResult.data,
                     id: `new-${nextId.current++}` // Generate new ID
                 });
             } else {
                 duplicateCount++;
             }
           }
         }

         if (validationErrors.length > 0) {
           toast({
             title: "Veri Doğrulama Hatası",
             description: (
                <div className="max-h-40 overflow-y-auto text-xs">
                    <p className="font-semibold mb-1">Aşağıdaki satırlarda hatalar bulundu:</p>
                    <ul>
                        {validationErrors.slice(0, 5).map((error, index) => <li key={index}>{error}</li>)}
                        {validationErrors.length > 5 && <li>...ve {validationErrors.length - 5} diğer satırda hata.</li>}
                    </ul>
                </div>
             ),
             variant: "destructive",
             duration: 10000,
           });
         } else if (newData.length === 0 && duplicateCount > 0) {
             toast({
                title: "Veri Yüklenmedi",
                description: `Dosyadaki tüm veriler (${duplicateCount} satır) mevcut listede zaten vardı. Yeni veri eklenmedi.`,
                variant: "default",
            });
         } else if (newData.length > 0) {
            setApprovalData((prevData) => [...prevData, ...newData]);
            toast({
                title: "Veri Yüklendi",
                description: `${newData.length} yeni veri başarıyla eklendi.` + (duplicateCount > 0 ? ` ${duplicateCount} tekrarlanan satır atlandı.` : ''),
            });
         } else {
             toast({
                title: "Veri Yüklenmedi",
                description: "Dosyada eklenecek geçerli veri bulunamadı.",
                variant: "default",
            });
         }

       } catch (error: any) {
         console.error("Error processing Excel file:", error);
         toast({
           title: "Dosya İşleme Hatası",
           description: error.message || "Excel dosyası işlenirken bir hata oluştu.",
           variant: "destructive",
         });
       } finally {
         setIsUploading(false);
         if (fileInputRef.current) fileInputRef.current.value = '';
       }
     };

     reader.onerror = (error) => {
       console.error("Error reading file:", error);
       setIsUploading(false);
       toast({ title: "Dosya Okuma Hatası", description: "Dosya okunurken bir hata oluştu.", variant: "destructive" });
       if (fileInputRef.current) fileInputRef.current.value = '';
     };

     reader.readAsArrayBuffer(file);
   };

   const downloadTemplate = () => {
     const ws = XLSX.utils.aoa_to_sheet([excelHeaders]);
     ws['!cols'] = [
        { wch: 20 },
        { wch: 25 },
        { wch: 15 },
        { wch: 15 },
        { wch: 25 }
     ];
     const wb = XLSX.utils.book_new();
     XLSX.utils.book_append_sheet(wb, ws, "Veri Şablonu");
     XLSX.writeFile(wb, "Seri_Tadilat_Tip_Onay_Sablonu.xlsx");
      toast({
        title: "Şablon İndirildi",
        description: "Excel veri yükleme şablonu başarıyla indirildi.",
      });
   };

   const sortedApprovalData = useMemo(() => {
        return [...approvalData].sort((a, b) => {
            if (a.marka.toLowerCase() < b.marka.toLowerCase()) return -1;
            if (a.marka.toLowerCase() > b.marka.toLowerCase()) return 1;
            if (a.tipOnayNo.toLowerCase() < b.tipOnayNo.toLowerCase()) return -1;
            if (a.tipOnayNo.toLowerCase() > b.tipOnayNo.toLowerCase()) return 1;
            return 0;
        });
   }, [approvalData]);


  return (
    <div className="flex min-h-screen items-start justify-center bg-gradient-to-br from-background to-muted/60 p-4 sm:p-8">
       <Card className="w-full max-w-6xl shadow-xl bg-card/80 backdrop-blur-sm">
         <CardHeader>
           <div className="flex items-center justify-between mb-4">
             <Link href="/" passHref>
               <Button variant="outline" size="icon" aria-label="Geri">
                 <ArrowLeft className="h-4 w-4" />
               </Button>
             </Link>
             <div className="flex-1 text-center">
               <CardTitle className="text-2xl font-bold text-primary">
                 Seri Tadilat Tip Onay Verileri
               </CardTitle>
               <CardDescription className="text-muted-foreground">
                 Onaylanmış seri tadilat tip verilerini görüntüleyin, yenilerini ekleyin veya Excel ile toplu yükleyin.
               </CardDescription>
             </div>
             <div className="w-10"></div>
           </div>
           <div className="flex flex-col sm:flex-row justify-center items-center gap-2 mt-4">
                <Button onClick={triggerFileInput} disabled={isUploading || isFormVisible}>
                 {isUploading ? (
                   <Loader2 className="mr-2 h-4 w-4 animate-spin" />
                 ) : (
                   <FileUp className="mr-2 h-4 w-4" />
                 )}
                 {isUploading ? 'Yükleniyor...' : 'Excel Yükle'}
               </Button>
               <Input
                   ref={fileInputRef}
                   type="file"
                   accept=".xlsx, .xls, .csv"
                   onChange={handleFileChange}
                   className="hidden"
                   id="excel-upload"
               />
               <Button variant="outline" onClick={downloadTemplate} disabled={isUploading || isFormVisible}>
                 <Download className="mr-2 h-4 w-4" />
                 Şablon İndir
               </Button>
            </div>
         </CardHeader>
         <CardContent className="space-y-6">
           {!isFormVisible && !isUploading && (
             <div className="flex flex-wrap justify-center gap-2">
               <Button onClick={() => setIsFormVisible(true)}>
                 <PlusCircle className="mr-2 h-4 w-4" />
                 Yeni Tek Veri Ekle
               </Button>
               {selectedRows.size > 0 && (
                   <AlertDialog>
                   <AlertDialogTrigger asChild>
                       <Button variant="destructive" disabled={isUploading}>
                       <Trash2 className="mr-2 h-4 w-4" />
                       Seçili ({selectedRows.size}) Sil
                       </Button>
                   </AlertDialogTrigger>
                   <AlertDialogContent>
                       <AlertDialogHeader>
                       <AlertDialogTitle>
                           <AlertTriangle className="inline-block mr-2 h-5 w-5 text-destructive" /> Seçili Verileri Silme İşlemini Onayla
                       </AlertDialogTitle>
                       <AlertDialogDescription>
                           Bu işlem geri alınamaz. Seçili {selectedRows.size} veriyi kalıcı olarak silmek istediğinizden emin misiniz?
                       </AlertDialogDescription>
                       </AlertDialogHeader>
                       <AlertDialogFooter>
                       <AlertDialogCancel>İptal</AlertDialogCancel>
                       <AlertDialogAction
                           onClick={handleDeleteSelected}
                           className="bg-destructive hover:bg-destructive/90 text-destructive-foreground"
                       >
                           Seçili ({selectedRows.size}) Sil
                       </AlertDialogAction>
                       </AlertDialogFooter>
                   </AlertDialogContent>
                   </AlertDialog>
               )}
               {approvalData.length > 0 && (
                    <AlertDialog>
                    <AlertDialogTrigger asChild>
                        <Button variant="outline" className="border-destructive text-destructive hover:bg-destructive/10" disabled={isUploading}>
                        <Trash2 className="mr-2 h-4 w-4" />
                        Tümünü Sil ({approvalData.length})
                        </Button>
                    </AlertDialogTrigger>
                    <AlertDialogContent>
                        <AlertDialogHeader>
                        <AlertDialogTitle>
                            <AlertTriangle className="inline-block mr-2 h-5 w-5 text-destructive" /> Tüm Verileri Silme İşlemini Onayla
                        </AlertDialogTitle>
                        <AlertDialogDescription>
                            Bu işlem geri alınamaz. Listedeki tüm verileri kalıcı olarak silmek istediğinizden emin misiniz?
                            Bu işlem {approvalData.length} kaydı silecektir.
                        </AlertDialogDescription>
                        </AlertDialogHeader>
                        <AlertDialogFooter>
                        <AlertDialogCancel>İptal</AlertDialogCancel>
                        <AlertDialogAction
                            onClick={handleDeleteAll}
                            className="bg-destructive hover:bg-destructive/90 text-destructive-foreground"
                        >
                            Tümünü Sil
                        </AlertDialogAction>
                        </AlertDialogFooter>
                    </AlertDialogContent>
                    </AlertDialog>
               )}
             </div>
           )}

           {isFormVisible && (
             <Form {...form}>
               <form onSubmit={form.handleSubmit(onSubmit)} className="space-y-4 border p-4 rounded-lg shadow-sm bg-card relative">
                 <div className="flex justify-between items-center mb-3 border-b pb-2">
                   <h3 className="text-lg font-semibold text-foreground">Yeni Tek Veri Ekle</h3>
                   <Button
                     type="button"
                     variant="ghost"
                     size="icon"
                     onClick={handleCancel}
                     className="text-muted-foreground hover:text-destructive"
                     aria-label="Formu Kapat"
                   >
                     <X className="h-5 w-5" />
                   </Button>
                 </div>
                 <div className="grid grid-cols-1 md:grid-cols-3 lg:grid-cols-5 gap-4">
                   <FormField
                     control={form.control}
                     name="marka"
                     render={({ field }) => (
                       <FormItem>
                         <FormLabel>Marka</FormLabel>
                         <FormControl>
                           <Input placeholder="Örn: Marka A" {...field} />
                         </FormControl>
                         <FormMessage />
                       </FormItem>
                     )}
                   />
                   <FormField
                     control={form.control}
                     name="tipOnayNo"
                     render={({ field }) => (
                       <FormItem>
                         <FormLabel>Tip Onay No</FormLabel>
                         <FormControl>
                           <Input placeholder="Örn: e3*... veya AB1*..." {...field} />
                         </FormControl>
                         <FormMessage />
                       </FormItem>
                     )}
                   />
                   <FormField
                     control={form.control}
                     name="varyant"
                     render={({ field }) => (
                       <FormItem>
                         <FormLabel>Varyant</FormLabel>
                         <FormControl>
                           <Input placeholder="Örn: CXE1A" {...field} />
                         </FormControl>
                         <FormMessage />
                       </FormItem>
                     )}
                   />
                   <FormField
                     control={form.control}
                     name="versiyon"
                     render={({ field }) => (
                       <FormItem>
                         <FormLabel>Versiyon</FormLabel>
                         <FormControl>
                           <Input placeholder="Örn: TFB7R" {...field} />
                         </FormControl>
                         <FormMessage />
                       </FormItem>
                     )}
                   />
                    <FormField
                     control={form.control}
                     name="seriTadilatTipOnayi"
                     render={({ field }) => (
                       <FormItem>
                         <FormLabel>Seri Tadilat Tip Onayı</FormLabel>
                         <FormControl>
                           <Input placeholder="Örn: STTO-123" {...field} />
                         </FormControl>
                         <FormMessage />
                       </FormItem>
                     )}
                   />
                 </div>
                 <div className="flex justify-end space-x-2 pt-4">
                    <Button type="button" variant="outline" onClick={handleCancel}>
                      İptal
                    </Button>
                   <Button type="submit" disabled={form.formState.isSubmitting} className="min-w-[100px]">
                     {form.formState.isSubmitting ? (
                       <div className="flex items-center justify-center">
                         <Loader2 className="animate-spin -ml-1 mr-2 h-4 w-4 text-white" />
                          Ekleniyor...
                        </div>
                     ) : 'Kaydet'}
                   </Button>
                 </div>
               </form>
             </Form>
           )}

           <Separator className="my-6" />

           <div>
             <h3 className="text-lg font-semibold text-foreground mb-3 text-center">Mevcut Veri Listesi</h3>
              <Table>
                <TableCaption>
                  {approvalData.length > 0 ? `Toplam ${approvalData.length} kayıt.` : 'Henüz veri eklenmemiş.'}
                  {selectedRows.size > 0 && ` ${selectedRows.size} satır seçili.`}
                </TableCaption>
                <TableHeader>
                  <TableRow>
                    <TableHead className="w-[5%] px-2">
                       <Checkbox
                         id="select-all"
                         checked={headerCheckboxState}
                         onCheckedChange={handleSelectAll}
                         aria-label="Tümünü Seç/Bırak"
                       />
                     </TableHead>
                    <TableHead className="w-[15%]">Marka</TableHead>
                    <TableHead className="w-[20%]">Tip Onay No</TableHead>
                    <TableHead className="w-[15%]">Varyant</TableHead>
                    <TableHead className="w-[15%]">Versiyon</TableHead>
                    <TableHead className="w-[20%]">Seri Tadilat Tip Onayı</TableHead>
                    <TableHead className="w-[10%] text-right pr-4">İşlemler</TableHead>
                  </TableRow>
                </TableHeader>
                <TableBody>
                  {sortedApprovalData.length === 0 ? (
                     <TableRow>
                       <TableCell colSpan={7} className="h-24 text-center text-muted-foreground">
                         Henüz veri eklenmemiş. Yeni veri ekleyin veya Excel dosyası yükleyin.
                       </TableCell>
                     </TableRow>
                   ) : (
                      sortedApprovalData.map((data) => (
                        <TableRow
                          key={data.id}
                          data-state={selectedRows.has(data.id) ? 'selected' : undefined}
                        >
                          <TableCell className="px-2">
                             <Checkbox
                               id={`select-row-${data.id}`}
                               checked={selectedRows.has(data.id)}
                               onCheckedChange={(isChecked) => handleRowSelect(data.id, isChecked)}
                               aria-label={`Satır ${data.id} Seç`}
                             />
                          </TableCell>
                          <TableCell className="font-medium">{data.marka}</TableCell>
                          <TableCell>{data.tipOnayNo}</TableCell>
                          <TableCell>{data.varyant}</TableCell>
                          <TableCell>{data.versiyon}</TableCell>
                          <TableCell>{data.seriTadilatTipOnayi}</TableCell>
                          <TableCell className="text-right pr-4">
                            <AlertDialog>
                              <AlertDialogTrigger asChild>
                                <Button variant="ghost" size="icon" className="text-destructive hover:text-destructive/80" aria-label="Veriyi Sil">
                                  <Trash2 className="h-4 w-4" />
                                </Button>
                              </AlertDialogTrigger>
                              <AlertDialogContent>
                                <AlertDialogHeader>
                                  <AlertDialogTitle>
                                    <AlertTriangle className="inline-block mr-2 h-5 w-5 text-destructive" /> Silme İşlemini Onayla
                                    </AlertDialogTitle>
                                  <AlertDialogDescription>
                                    Bu işlem geri alınamaz. Seçili veriyi kalıcı olarak silmek istediğinizden emin misiniz?
                                    <br />
                                    <strong>Marka:</strong> {data.marka}, <strong>Tip Onay No:</strong> {data.tipOnayNo}
                                  </AlertDialogDescription>
                                </AlertDialogHeader>
                                <AlertDialogFooter>
                                  <AlertDialogCancel>İptal</AlertDialogCancel>
                                  <AlertDialogAction
                                    onClick={() => handleDeleteSingle(data.id)}
                                    className="bg-destructive hover:bg-destructive/90 text-destructive-foreground"
                                  >
                                    Sil
                                  </AlertDialogAction>
                                </AlertDialogFooter>
                              </AlertDialogContent>
                            </AlertDialog>
                          </TableCell>
                        </TableRow>
                      ))
                   )}
                </TableBody>
              </Table>
            </div>
          </CardContent>
      </Card>
    </div>
  );
}
